import request from '@/utils/request'
// 发布商品
export function getBargainActivityList(data) {
  return request({
    url: `/sys/business/shopActivity/getBargainActivityList`,
    method: 'post',
    data: data
  })
}
// 推广
export function getBargainActivityImg(data) {
  return request({
    url: `/sys/business/shopActivity/getBargainActivityImg?shopId=${data.shopId}&productId=${data.productId}`,
    method: 'get',
  })
}
// 组件 - 列表
export function getBargainSubgroupProductPage(data) {
  return request({
    url: `/sys/business/shopActivity/getBargainSubgroupProductPage`,
    method: 'post',
    data: data
  })
}
// 组件 - 回显
export function getBargainSubgroupInfo(ids) {
  return request({
    url: `/sys/business/shopActivity/getBargainSubgroupInfo?bargainIds=${ids}`,
    method: 'get',
  })
}