<template>
  <div class="itemBox">
    <p class="title">砍价</p>
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="砍价专区名称">
        <el-input v-model="itemData.title" maxlength="10" show-word-limit></el-input>
      </el-form-item>
      <el-form-item label="添加活动商品" class="navigation">
        <div class="goodsUpload" @click="bargainCommodityShow = true">+</div>
        <ul class="goodsUploadList" v-show="itemData.goodsData.length!=0">
          <li
            v-for="(item,index) in itemData.goodsData"
            :key="index"
            @mouseover="mouseOver(index)"
            @mouseleave="mouseLeave"
            v-show="!item.showFalse">
            <img :src="item.imageUrl" alt />
            <img
              class="delBtn"
              :class="hoverIndex==index?'delBtnShow':''"
              src="@/assets/decorateBox/goods/close.png"
              @click="delGoods(index)"
              alt
            />
          </li>
        </ul>
      </el-form-item>
      <el-form-item label="列表样式" class="navigation">
        <el-radio-group v-model="itemData.listType" @change="showType">
          <el-radio :label="0">圆角</el-radio>
          <el-radio :label="1">圆角有投影</el-radio>
          <el-radio :label="2">直角</el-radio>
          <el-radio :label="3">直角有投影</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="显示内容" class="checkboxGroup">
        <el-checkbox v-model="checked" disabled>商品名称</el-checkbox>
        <el-checkbox v-model="checked" disabled>底价</el-checkbox>
        <el-checkbox v-model="checked" disabled>去砍价按钮</el-checkbox>
        <el-checkbox v-model="itemData.lineation">划线原价</el-checkbox>
        <el-checkbox v-model="itemData.showMore">查看更多</el-checkbox>
        <p>店铺所有参与砍价活动的商品可以在【查看更多】中的【全部砍价】页面展示。建议显示</p>
      </el-form-item>
    </el-form>
    <!-- 商品列表弹出框 -->
    <bargainCommodity 
      :bargainCommodityShow="bargainCommodityShow" 
      :bargainIds="itemData.bargainIds.join(',')" 
      @bargainCommodityClose="bargainCommodityClose" 
      @bargainCommodityFun="bargainCommodityFun"></bargainCommodity>
  </div>
</template>

<script>
import bargainCommodity from '@/components/popover/bargainCommodity';

export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      bargainCommodityShow:false,     //
      checked: true,
      hoverIndex: -1, //hover的index
      loadingbut: false, //审核提交加载中
      loadingbuttext: "确 定",
      loading: false,
      bargainIds:''
    };
  },
  methods: {
    // 
    bargainCommodityClose(){
      this.bargainCommodityShow = false;
    },
    bargainCommodityFun(e){
      console.log('父',e)
      if(this.itemData.goodsData[0].showFalse) this.itemData.goodsData = []
      e.forEach( item => {
        this.itemData.goodsData.push(item)
        this.itemData.bargainIds.push(item.bargainId)
      })
      console.log('选择完数据后的商品列表',this.itemData.goodsData)
      this.bargainCommodityShow = false;
    },
    //列表样式
    showType() {
      switch (this.itemData.listType) {
        case 0:
          // 圆角
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 1:
          // 圆角有投影
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle =
            "0px 2px 2px 0px rgba(153,153,153,0.1)";
          break;
        case 2:
          // 直角
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 3:
          // 直角有投影
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle =
            "0px 2px 2px 0px rgba(153,153,153,0.1)";
          break;
        case 4:
          // 极简直角
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 5:
          // 极简圆角
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
      }
    },
    // 移入事件
    mouseOver(index) {
      this.hoverIndex = index;
    },
    // 移出事件
    mouseLeave() {
      this.hoverIndex = -1;
    },
    // 删除商品
    delGoods(index) {
      this.itemData.goodsData.splice(index, 1);
      this.itemData.bargainIds.splice(index, 1);
      if(this.itemData.goodsData.length == 0){
        this.itemData.goodsData.push({
          productName:'这里是商品名称，最多展示2行，超出将...',
          activityPrice:'688.00',
          minPrice:'999.99',
          imageUrl:'',
          showFalse:true,
          activityStatus:1
        })
      }
    }
  },
  components:{
    bargainCommodity
  }
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  padding: 20px 18px 24px 18px;
  border-bottom: 1px solid #eee;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.el-dialog__body {
  .pagination {
    padding-top: 20px;
  }
  .search {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .el-form-item {
      margin-bottom: 0px;
      margin-right: 20px;
    }
    .formBtnBox {
      margin-left: 20px;
    }
  }
  img {
    width: 100px;
    margin: 0 auto;
  }
}

.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .checkboxGroup .el-form-item__content {
    margin-left: 10px;
  }
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .navigation {
    border-bottom: 1px solid #eee;
    padding-bottom: 16px;
    .el-radio {
      margin-left: 10px;
      width: 64px;
      margin-bottom: 10px;
    }
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }
  .el-checkbox-group {
    margin-left: 10px;
  }
  .el-checkbox {
    display: block;
    margin: 0;
  }
  .prompt {
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 20px;
    letter-spacing: 0px;
    margin-top: 5px;
  }
  .goodsUploadList {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 68px;
      height: 68px;
      transition: 0.2s;
      cursor: pointer;
      border: 1px solid #ddd;
      box-sizing: border-box;
      padding: 4px;
      position: relative;
      margin-right: 5px;
      margin-bottom: 10px;
      img {
        width: 100%;
        height: 100%;
      }
      .delBtn {
        width: 16px;
        height: 16px;
        position: absolute;
        right: -8px;
        top: -8px;
        display: none;
      }
      .delBtnShow {
        display: block;
      }
    }
    li:hover {
      border: 1px solid #6babec;
    }
  }
  .goodsUpload {
    font-size: 34px;
    background: #fcfcfc;
    font-weight: 100;
    color: #9a9a9a;
    width: 68px;
    height: 68px;
    border-radius: 4px 4px 4px 4px;
    text-align: center;
    line-height: 68px;
    border: 1px dashed #ddd;
    margin-bottom: 10px;
    transition: 0.2s;
    cursor: pointer;
  }
  .goodsUpload:hover {
    background-color: #ecf5ff;
    border: 1px dashed rgba(0, 121, 253, 1);
  }
}
</style>
